import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OutboundComponent } from './outbound/outbound.component';
import { Route, RouterModule } from '@angular/router';
import { MatBadgeModule } from '@angular/material/badge';
import { MatIconModule } from '@angular/material/icon';
import { MatCardModule } from '@angular/material/card';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { SharedModule } from 'src/app/shared/shared.module';
import { WidgetsModule } from 'src/app/widgets/chq-widgets.module';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { CheckInTaskComponent } from './check-in-task/check-in-task.component';
import { MatDialogModule } from '@angular/material/dialog';
import { DxDateBoxModule } from 'devextreme-angular';
import { TranslateModule } from '@ngx-translate/core';
import { MatTooltipModule } from '@angular/material/tooltip';
import { DirectiveModule } from 'src/app/helper/directive.module';

const outboundRoutes: Route[] = [
  {
    path: '',
    component: OutboundComponent,
    resolve: { version: VersionControlResolver }
  },
];

@NgModule({
  declarations: [
    OutboundComponent,
    CheckInTaskComponent
  ],
  imports: [
    CommonModule,
    WidgetsModule,
    SharedModule,
    MatDatepickerModule,
    MatCardModule,
    MatIconModule,
    MatTooltipModule,
    MatBadgeModule, 
    MatDialogModule,
    DxDateBoxModule,
    RouterModule.forChild(outboundRoutes),
    TranslateModule,
    DirectiveModule
  ]
})
export class OutboundModule { }
